import {React, useState, useContext, useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Slider from '@material-ui/core/Slider';
import { propTypes } from 'react-bootstrap/esm/Image';

import {AppContext} from '../../../Resources/CanvasContextFile';
import {useSelector} from "../../../Redux"
import { isObjectEmpty } from '../../../Resources/globalFunctions';
import { GetStateFromData } from '../../../components/CustomHooks/CustomHooks';

const useStyles = makeStyles((theme) => ({
  // root: {
  //   width: 250,
  // },
  // margin: {
  //   height: theme.spacing(3),
  // },
  sliderRoot: {
    width: "245px",
    marginLeft: "4px",
    marginBottom: "15px",
    '& .MuiSlider-track':{
      height:"4px",
      backgroundColor: "#0072C6", 
    },
    '& .MuiSlider-rail':{
      height:"4px"
    }, 
    '& .MuiSlider-thumb':{
      height:"13px",
      width:"13px",
      backgroundColor:"#0072C6",
      border: "2px solid #0072C6",
    },
    '& .MuiSlider-mark MuiSlider-markActive':{
    },
    '& .MuiSlider-mark':{
      backgroundColor:"#ffffff",
      display: "none"
    },
    '& .MuiSlider-markLabel MuiSlider-markLabelActive':{
      left:"19% !important"
    },
    '& .MuiSlider-markLabel':{
      left:"95%"
    },
    '& .MuiInput-underline:before':{
      width:"35px",
      height:"2px",
      // marginBottom:"-10px"
    },
    '& .MuiInput-underline:after':{
      width:"35px",
      height:"2px",
      // marginBottom:"-10px"
    },
    '& .MuiInputBase-input':{
      width:"38px",
      height:"8px",
      fontSize:"12px",
      marginBottom:"-5px",
      marginLeft:"3px"
    } 
  }
}));

export default function DiscreteSlider(props) {
  
  const classes = useStyles();
  const {selectedControl, selectKey, min, max, step, defaultValue, marks} = props;
  const [val,setVal] = useState(    selectKey === "w" || selectKey === "md" || selectKey === "sm" || selectKey === "xs" ? selectedControl?.[selectKey] : selectedControl?.style?.[selectKey]
  );


  // const contexts = useSelector(
  //   state=>({
  //       contexts:state.contexts
  //   })
  // );
  // const { updateJson} = useContext(AppContext);
  // const { updatedComponentInFormState} = updateJson;

  const contexts = GetStateFromData(props.containerId);
  
  const {updateJson, device}=contexts.contexts;
  const {updatedComponentInFormState} =updateJson;

  useEffect(() => {
  if(!isObjectEmpty(selectedControl))
    setVal(selectKey === "w" || selectKey === "md" || selectKey === "sm" || selectKey === "xs" ? selectedControl?.[selectKey] : selectedControl?.style?.[selectKey]);
  },[JSON.stringify(selectedControl)]);

  const handleChange = (event, value) => {
    setVal(()=>value);
    if(selectKey === "w" || selectKey === "sm" || selectKey === "xs" ){
      if((device === "web" && selectKey === "w") || (device === "tablet" && selectKey === "sm") || (device === "mobile" && selectKey === "xs")){
        selectedControl[selectKey] = value;
        selectedControl["md"] = value;
      } else {
        selectedControl[selectKey] = value;
      }
    } else {
      selectedControl.style[selectKey] = value;
    }
  }

  function handleMouseUp() {
    updatedComponentInFormState(selectedControl.id , selectedControl)
  }

  return (
    <div className={classes.root}>
      <Slider
        containerId={props.containerId}
        classes={{
          root: classes.sliderRoot
        }}
        step={step ? step : 1}
        min={min ? min: 0}
        max={max ? max: 100}
        defaultValue={defaultValue ? defaultValue: 0}
        valueLabelDisplay="auto"
        marks={marks ? marks : null}
        value={val}
        onChange={handleChange}
        onClick={handleMouseUp}
      />
    </div>
  );
}
