import React, { useState, useContext, useEffect, useLayoutEffect } from 'react';
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import {Card,Row,Col,Form} from 'react-bootstrap';
import ToggleBtn from '../../../../ReusableComponents/ToggleBtn/ToggleBtn';
import ControlsMap from '../../../Properties/PropertiesConfiguration';
import CollapsibleComponent from '../../../../ReusableComponents/CollapseCard/CollapseCard';
import COLPropertiesJson from "../../../Properties/COLPropertiesJson";
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import ControlLabel from '../../../../ReusableComponents/ControlLabel/ControlLabel';
import {downloadJson,isColor} from "../../../../Resources/globalFunctions";
import { useTranslation } from 'react-i18next'
import {useDispatch,addContexts,useSelector} from "../../../../Redux"
import {AppContext} from '../../../../Resources/CanvasContextFile'
import Slider from "../../../../UI/BasicFormControls/Slider/Slider";
import ActiveIcon from "../../../../ReusableComponents/ActiveIcon/ActiveIcon";
import CustomizedAlert from "../../../../ReusableComponents/Alert/CustomizedAlert";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import { regexValidator } from "../../../../Resources/globalFunctions";
import { validateCustomControlId } from "../../RequestLogic";
import { updateUserId } from "../../Logic/UserIdLogic";
import { GetStateFromData } from '../../../CustomHooks/CustomHooks';

const ColumnProperties = (props) => {
  const translationStyles1=window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA";
  const contexts = GetStateFromData(props.containerId);
  
    const { t } = useTranslation(['styles', 'translation']);
    const {updateJson, changeSectionLayout, device, formState2, deleteControl,setQuickToolsClose,customControlIdList,setCustomControlIdList,currentDevice, getElementJson, showAlertConfirmNotifications }=contexts.contexts;
    const { updatedComponentInFormState, removeAndUpdateComponentInFormState} =updateJson;
    const [errorMessageId, setErrorMessageId] = useState(false);  
    const [alertData, setAlertData] = useState({
      open: false,
      showConfirm: "showNotification"
  });
    var globalTheme=formState2.theme;

    
    if(props.selectedControl.style && props?.selectedControl?.style?.["heightselect"] === undefined ){
        props.selectedControl.style["heightselect"] = "set";
    }
    
    // const { updateJson,deleteControl } = useContext(AppContext);
    // const { updatedComponentInFormState, removeAndUpdateComponentInFormState} =updateJson;
    // const {changeSectionLayout} = useContext(AppContext);
    // const AppContextVar = useContext(AppContext);
    // var currentDevice;
    
    // if(AppContextVar!==undefined){
    //     globalTheme = AppContextVar.formState2 && AppContextVar.formState2.theme;
    //     currentDevice = AppContextVar.device;
    // }


    // console.log("Column")
    // console.log(props.selectedControl);

    var CollapsibleItems = [];
    Object.keys(COLPropertiesJson).forEach((key,index)=>{
        var collapseComponentBody = [];
        Object.keys(COLPropertiesJson[key]).forEach((property)=>{
            const fetchedProperty = property.replace(/\d+/g, "");
            // console.log("col props..", property,COLPropertiesJson[key],COLPropertiesJson[key][property])
            collapseComponentBody.push(prepareHtmlTag(fetchedProperty,COLPropertiesJson[key][property],ControlsMap[fetchedProperty].type));    
        })
        CollapsibleItems.push(
          <CollapsibleComponent id={index+""} heading={key} body={collapseComponentBody}/>
        );
    })

   // console.log("props..", device);


    // function handleProperty(propertyName, propertyValue, event) {
    //     console.log("Col props..", propertyName, propertyValue);

    //     let temp_style = props.selectedControl.style;
    //     temp_style[propertyName] = propertyValue;
    //     props.selectedControl.style[propertyName] = propertyValue;
    
    //     updatedComponentInFormState(
    //       props.selectedControl.id,
    //       props.selectedControl
    //     );   
    // }

    function handleProperty(propertyName, propertyValue, event) {
        //console.log(propertyValue);
    
       // console.log("propertyNameMulti ", propertyName);
       // console.log("propertyNameMulti ", propertyValue);

        if(propertyName.includes("colheight")) {
          //  console.log("prop..", props.selectedControl, props.selectedControl.id, document.getElementById(props.selectedControl.id),document.getElementById(props.selectedControl.id).offsetHeight);
            props.selectedControl.style["prevheight"] = document.getElementById(props.selectedControl.id).offsetHeight;
            props.selectedControl.style[propertyName] = propertyValue;
            props.selectedControl.style.colheight1 = propertyValue;
            let parentRow = event.currentTarget.closest(`[elem_type=row]`);
            // let json = getElementJson(formState2, parentRow, "row")
            // console.log("prop..", parentRow);   
        } else {
            let temp_style = props.selectedControl.style;
            temp_style[propertyName] = propertyValue;
            props.selectedControl.style[propertyName] = propertyValue;
            if(propertyValue === "auto"){
              props.selectedControl.style.colheight = "auto";
            }
            else{
              props.selectedControl.style.colheight = props.selectedControl.style?.colheight1;
            }
        }
    
       
    
    
        if(propertyName.includes('Label')){
    
      
          if(propertyValue==="Regular Italic"){
              props.selectedControl.style[propertyName] = "normal";
              props.selectedControl.style["LabelFontStyle"] = "italic";
              }else if(propertyValue==="Semibold"){
              props.selectedControl.style[propertyName] = "600";
              props.selectedControl.style["LabelFontStyle"] = "normal";
              }else if(propertyValue==="Bold"){
              props.selectedControl.style[propertyName] = "bold";
              props.selectedControl.style["LabelFontStyle"] = "normal";
              }else if(propertyValue==="Regular"){
              props.selectedControl.style[propertyName] = "normal";
              props.selectedControl.style["LabelFontStyle"] = "normal";
             }
      
        } else {
            if(propertyValue==="Regular Italic"){
                props.selectedControl.style[propertyName] = "normal";
                props.selectedControl.style["FontStyle"] = "italic";
            } else if(propertyValue==="Semibold"){
            props.selectedControl.style[propertyName] = "600";
            props.selectedControl.style["FontStyle"] = "normal";
            } else if(propertyValue==="Bold"){
            props.selectedControl.style[propertyName] = "bold";
            props.selectedControl.style["FontStyle"] = "normal";
            } else if(propertyValue==="Regular"){
                props.selectedControl.style[propertyName] = "normal";
                props.selectedControl.style["FontStyle"] = "normal";
            }
        }

        updatedComponentInFormState(
          props.selectedControl.id,
          props.selectedControl
        );
    
        try {
          if (propertyName === "CustomId") {
            if (propertyValue !== props.selectedControl.style.CustomId) {
              var element = event.target;
              console.log(
                "id",
                props.selectedControl,
                "propertyValue::" + propertyValue
              );
              var jsonToSend = {
                customControlId: propertyValue,
                controlid: props.selectedControl.id,
              };
              validateCustomControlId(jsonToSend)
                .then((response) => {
                  if (response && response.data && response.data.status === "0") {
                    let temp_style = props.selectedControl.style;
                    // temp_style[propertyName] = propertyValue;
                    // props.selectedControl.style[propertyName] = propertyValue;
                    let updatedJson = updateUserId(
                      props.selectedControl, // whole control Json
                      propertyValue //newly defined id
                      // temp_style[propertyName], //getting previous ControlId
                      // undefined // precedence from formState
                    );
                    if (updatedJson) {
                      updatedComponentInFormState(
                        props.selectedControl.id,
                        updatedJson
                      );
                    }
                  } else if (
                    response &&
                    response.data &&
                    response.data.status === "-1"
                  ) {
                    showAlertConfirmNotifications({
                      'open':true,
                      'showConfirm':'showNotification',
                      'alertType':'error',
                      'alertContentKey':"CustomControl Id already Exist"
                    });
                    element.value = props.selectedControl.style.CustomId || "";
                  }
                })
                .catch(function (error) {
                  // handle error
                  element.value = props.selectedControl.style.CustomId || "";
                  console.log(error);
                  console.log("Server side error " + error);
                });
            }
          } 
          // else if (
          //   event &&
          //   event.which !== 8 &&
          //   event.which !== 46 &&
          //   !regexValidator(
          //     String.fromCharCode(event.which),
          //     ControlsMap[propertyName]["regexType"]
          //   )
          // ) {
          //   event.preventDefault();
          //   //setErrorMessageId(inputBoxId);
          //   setAlertData({
          //     ...ControlsMap["customizedAlertNotificationData"],
          //     open: true,
          //     alertContentKey: "RegexErrorMessage",
          //   });
          // } 
          else {
            let temp_style = props.selectedControl.style;
            temp_style[propertyName] = propertyValue;
            props.selectedControl.style[propertyName] = propertyValue;
            updatedComponentInFormState(
              props.selectedControl.id,
              props.selectedControl
            );
            setErrorMessageId(false);
          }
        } catch (e) {
        }
        
    }

    function validateRegex(propertyName, propertyValue, event, inputBoxId) {
        try {
          if (
            event &&
            event.which !== 8 &&
            event.which !== 46 &&
            event.code !== "Tab" &&
            !regexValidator(
              String.fromCharCode(event.which),
              ControlsMap[propertyName]["regexType"]
            )
          ) {
            event.preventDefault();
            // setErrorMessageId(inputBoxId);
            setAlertData({
              ...ControlsMap["customizedAlertNotificationData"],
              open: true,
              alertContentKey: "RegexErrorMessage",
            });
          } else {
            setErrorMessageId(false);
          }
        } catch (e) {
          console.log("Error while validating regex");
        }
      }



    function selectedOptionForLabel(selectedControlId,selectedControlLabel,ControlLabelKey)
    {
      
       var select = document.getElementById(selectedControlId+ControlLabelKey);
       var option_tag = select.options[select.selectedIndex];
              
        let temp_style = props.selectedControl;
        temp_style[ControlLabelKey] = parseInt(option_tag.value);

        if(props.selectedControl["style"] === undefined){
            props.selectedControl["style"] = {}
        }
        if(ControlLabelKey === "w" || ControlLabelKey === "sm" || ControlLabelKey === "xs") {
            if((device === "web" && ControlLabelKey === "w") || (device === "tablet" && ControlLabelKey === "sm") || (device === "mobile" && ControlLabelKey === "xs")){
                props.selectedControl[ControlLabelKey] = parseInt(option_tag.value);
                props.selectedControl["md"] = parseInt(option_tag.value);
            } else {
                props.selectedControl[ControlLabelKey] = parseInt(option_tag.value);
            }
        } else {
            props.selectedControl["style"][ControlLabelKey] = parseInt(option_tag.value);
        }
        
        

        if (currentDevice!==undefined){
           // console.log("COl props..", "hello",  currentDevice)
            if(currentDevice==='web'){
                props.selectedControl["w"] = parseInt(option_tag.value); 
            } else if(currentDevice==='tablet'){
                props.selectedControl['sm'] =  parseInt(option_tag.value); 
            } else if(currentDevice==='mobile') {
                props.selectedControl['xs'] =  parseInt(option_tag.value); 
            }
        } else {
            console.log('Issue with the device selector!');
        }
    
       // console.log("COl props..", props.selectedControl.id);
        updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
       
    }

   // console.log("COl props..", props.selectedControl, currentDevice);
    
   function handleToggle(e, field) {
    const value = e.target.value;

    props.selectedControl.style[field] = value;

    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

    function prepareHtmlTag(key,value,type) {
        
        switch(type.toLowerCase()){
            case "header" : return(
                <p className={styles.propertiesHeaderColumn}>{t(`translation:${ControlsMap[key].label}`)}</p>
            )

            case "toggle":
              return (
                <div
                  className={
                    value.field === "showIcon" ? "customToggleBtn1" : "toggleBtn"
                  }
                >
                  <span> {t(`translation:${ControlsMap[key].label}`)} </span>
                  <ToggleBtn
                    key={props.selectedControl.id}
                    handleToggle={handleToggle}
                    field={value.field}
                    toggleState={props?.selectedControl?.style?.[value.field]}
                  />
                </div>
              );
    
            case "combo" : return(
                <>
                <div className={styles.parentDiv} style={translationStyles1?{display:"flex"}:null}>
                    <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
                    <div className={styles.ColumnSelectForText1}>
                        <select 
                            id={props.selectedControl.id+key} 
                            value={(key === "w" || key === "sm" || key === "xs") ? props.selectedControl[key] : props.selectedControl.style[key]}
                            className={styles.ColumnSelectForText2} 
                            onChange={()=>selectedOptionForLabel(props.selectedControl.id,ControlsMap[key].label,key)}
                        >
                        {ControlsMap[key].children.map((optionTagData,optionCount)=>{
                            if(ControlsMap[key].children[optionCount].Value){
                                return <> 
                                {ControlsMap[key].children[optionCount].Value ===  props.selectedControl[key] ?
                                <option style={ControlsMap[key].children[optionCount]?.style} value={ControlsMap[key].children[optionCount].Value} selected>
                                  
                                  {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}
                                </option>
                                :
                                <option style={ControlsMap[key].children[optionCount]?.style} value={ControlsMap[key].children[optionCount].Value} >{t(`translation:${ControlsMap[key].children[optionCount].Title}`)}</option>} 
                                </>
                            }
                        })}
                        </select>
                    </div>
                </div>
                </>
            )  
            
            case "slider":  
            return(
                <div
                key={props.selectedControl && props.selectedControl.style && props.selectedControl.style[value.field]}
                >
                    <Slider containerId={props.containerId} selectedControl={props.selectedControl} selectKey={value.field} min={1} max={12} marks={[{value: 1, label: '1'},{value: 12, label: '12'}]} />
                </div>
            )

            case "heightselect":
            return (
                <>
                    <div 
                    style={{
                        font: "normal normal normal 12px/12px Open Sans",
                        letterSpacing: "0px",
                        color: "#000000",
                        opacity: 1,
                    }}
                    > 
                    {t(`translation:${"Height"}`)} 
                    </div>
                    <ActiveIcon
                        activeStyle={{
                            padding: "12px 0",
                            width: "122px",
                            height: "40px",
                            border: "2px solid #0072C6",
                            borderRadius: "2px 0px 0px 2px",
                            background: "#fff",
                            margin: "0px",
                            color: "#0072C6",
                            font: "normal normal 600 12px/12px Open Sans",
                        }}
                        property={value.field}
                        inActiveStyle={{
                            padding: "12px 0",
                            width: "124px",
                            height: "40px",
                            border: "1px solid #C4C4C4",
                            borderRadius: "2px 0px 0px 2px",
                            background: "#fff",
                            margin: "0px",
                            color: "#C4C4C4",
                            font: "normal normal 600 12px/12px Open Sans",
                        }}
                        passedValue={props?.selectedControl?.style?.["heightselect"]}
                        defaultValue={props?.selectedControl?.style?.["heightselect"]}
                        handleStyleType={handleProperty}
                    >
                        <span value="auto">{t(`translation:${"Auto Height"}`)}</span>
                        <span value="set">{t(`translation:${"Set Height"}`)}</span>
                    </ActiveIcon>
                </>
            );
            
            case "text" : return(
                props?.selectedControl?.style?.["heightselect"] === "set"  &&
                <div className={styles.parentDiv} style={translationStyles1?{display:"flex"}:null}>
                    <span className={styles.leftLabel}> {t(`translation:${value.label}`)} </span>
                    <div className={styles.RightTextBox}>
                    {
                        <input 
                            type="text"  
                            aria-label={"Control Value1"}
                            defaultValue={props.selectedControl.style[key]} 
                            key={props.selectedControl.id}
                            id={props.selectedControl.id+key} 
                            className={[styles.RightLabelTextBox, errorMessageId === (props.selectedControl.id+key) ? styles.inputBoxErrorStyle : null].join(" ")}
                            // onKeyPress={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
                            // onKeyUp={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
                            onBlur={(event)=>handleProperty(key, event.target.value,event)}
                        />  
                    }
                    </div>
                    {errorMessageId === (props.selectedControl.id+key) ? <ErrorMessageComponent errorMessage="non-permissible value"/> : null}
                </div>
            )
        }
    }



    return (
        <>
            <ControlLabel label="Column" handleDelete={deleteControl} id={props.selectedControl.id} />
            <div className="propertiesBody">
                <Card.Body className={styles.cardBodySheetProperties}>
                {
                    CollapsibleItems.map((item, index) => {
                        return item;
                    })
                }
                </Card.Body>
            </div>
            {
              alertData.open === true &&
            <CustomizedAlert alertData={alertData} />
            }
        </>
    )

}

export default ColumnProperties;